import { EvaluatedNode } from 'publicodes'
import Explanation from '../Explanation'
import { useEngine } from '../hooks'
import { Mecanism } from './common/Mecanism'

export default function InversionNumrique({
	nodeValue,
	explanation,
}: EvaluatedNode<'inversion'>) {
	const engine = useEngine()

	return (
		<Mecanism name="inversion numrique" value={nodeValue}>
			{engine.cache.inversionFail && explanation.inversionGoal ?
				<>
					<p>
						Cette valeur devrait pouvoir tre estime  partir dune autre
						variable qui possde une formule de calcul et dont la valeur a t
						fixe dans la simulation :
					</p>
					<Explanation node={explanation.inversionGoal} />
					<p>
						Malheureusement, il a t impossible de retrouver une valeur pour
						cette formule qui permette datterrir sur la valeur demande.
					</p>
				</>
			: explanation.inversionGoal ?
				<>
					<p>
						Cette valeur a t estime  partir dune autre variable qui possde
						une formule de calcul et dont la valeur a t fixe dans la
						simulation :
					</p>
					<Explanation node={explanation.inversionGoal} />
				</>
			:	<>
					<p>
						Cette formule de calcul nexiste pas, mais on peut la calculer par
						inversion en utilisant les formules des rgles suivantes :
					</p>
					<ul id="inversionsPossibles">
						{explanation.inversionCandidates.map((el) => (
							<li key={el.dottedName}>
								<Explanation node={el} />
							</li>
						))}
					</ul>
				</>
			}
		</Mecanism>
	)
}
