import { EvaluatedNode } from 'publicodes'
import Explanation from '../Explanation'
import { Mecanism } from './common/Mecanism'

export default function UnePossibilitMecanism({
	explanation,
}: EvaluatedNode<'une possibilit'>) {
	return (
		<Mecanism name="une possibilit parmi" value={undefined}>
			<ul>
				{explanation.map((node, i) => (
					<li key={i}>
						<Explanation node={node} />
					</li>
				))}
			</ul>
		</Mecanism>
	)
}
